import "core-js/modules/es.object.to-string.js";
import "core-js/modules/web.dom-collections.for-each.js";
// components
import Carousel from "Shared/ts/components/carousel";
import Accordion from "Shared/ts/components/accordion"; // adapters

import SlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/slide-carousel"; // observers

import { observer } from "Shared/ts/observers/intersection"; // utils

import Disclosure from "Shared/ts/utils/disclosure";
import { initializeBase, initializeMicrosite, initializeToolTip } from "Shared/ts/applications/template";
import { initializeModalDialogIframe, initializeValidateCommon } from "Shared/ts/applications/form";
import AmazonBWPExpressCheckout from "ts/components/amazon-bwp-express-checkout";
import StatusScreen from "Shared/ts/components/status-screen";
import { importScrollableHeightByElement } from "Shared/ts/applications/navigation";
initializeBase();
initializeMicrosite();
initializeModalDialogIframe();

var initializeScrollableHeight = function initializeScrollableHeight() {
  var container = document.querySelector(".promo-header");
  if (!container) return;
  importScrollableHeightByElement(container);
};

initializeScrollableHeight();

var initializeExpressCheckout = function initializeExpressCheckout(root) {
  addEventListener("ECDrawFormComplete", function (event) {
    var ec = new AmazonBWPExpressCheckout(root);
    ec.createCheckoutButtonGUI();
    initializeToolTip();
  });
};

initializeExpressCheckout();

var initializeCheckoutForm = function initializeCheckoutForm() {
  var validateCommon = initializeValidateCommon();
  if (!validateCommon.form) return;
  var statusScreen = new StatusScreen("checkout-form", validateCommon.form);
  statusScreen.update("Verifying payment options");
  var closeConnection = false;
  var timeout;

  var initializeTimeout = function initializeTimeout(time) {
    return setTimeout(function () {
      closeConnection = true;
      clearTimeout(timeout);
      statusScreen.update("Ready for use");
    }, time);
  };

  addEventListener("load", function (event) {
    return initializeTimeout(0);
  });
};

initializeCheckoutForm();
var disclosure = new Disclosure();
disclosure.controllers.forEach(function (controller) {
  controller.addEventListener("click", function (event) {
    disclosure.toggleElementsByController(controller);
  });
});
observer(".slide--carousel", {
  inRange: function inRange(element) {
    var carousel = new Carousel(new SlideCarouselAdapter(element));
    carousel.enablePrevNextControls();
  }
});
observer(".accordion", {
  inRange: function inRange(element) {
    return new Accordion(element);
  }
});